import React from "react";
function OneHome(props) {

    return (
        <div className="col-md-3 col-sm-6">
            <div className="card" >
                <div className="card-header" >
                    < img src={props.info.imgo} alt="" />

                </div>
                <div className="card-body" >
                        <div><h3>{props.info.location}</h3></div>
                        <div><h3> Room : {props.info.numOfRoom}</h3></div>
                        <div><h3> Price : {props.info.price}</h3></div>
                        <div><h4> Phone : {props.info.phone}</h4></div>



                </div>

            </div>
        </div>
    )
}
export default OneHome